.bg-light {
  color: #55556b;
  background-color: $background;
}

a.bg-light {
  &:hover {
    color: #8f9cc0;
    background-color: #d5d9e4;
  }

  &:focus {
    color: #8f9cc0;
    background-color: #d5d9e4;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(244, 245, 248, 0.5);
  }

  &.focus {
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(244, 245, 248, 0.5);
  }
}

